.videoContainer{
    width: 720px;
    height: 560px;
    border-radius: 10px;
    background-color: black;
    position: relative;
}


.videoContainer video{
    border-radius: 10px;
}

.videoContainer canvas{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
}

.videoContainerHover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 200;
    background-color: rgba(0,0,0,0.8);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.progressContainer{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 200;
    display: flex;
    justify-content: center;
    align-items: center;
}

.detectionResult{
    position: absolute;
    top: 0;
    left: 0;
    width: auto;
    height: 40px;
    padding: 10px 20px;
    margin: 10px;
    background-color: white;
    border-radius: 9999px;
    z-index: 200;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading{
    height: 32px;
    width: 32px;
}

.loadingText{
    font-weight: bold;
    color: white;
    text-align: center;
}

.loadingContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.nameInput{
    width: 400px;
    padding: 5px 10px;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
}

.startButton{
    padding: 10px 30px;
    border-radius: 9999px;
    font-weight: bold;
    color: white;
    background-color: #a92cbf;
    border: none;
    font-size: 20px;
    cursor: pointer;
}