.outter{
    position: relative;
    width: 100%;
    height: 10px;
    background-color: #e5e5e5;
    border-radius: 10px;
    align-self: flex-start;
    margin: 10px;
}

.inner{
    position: absolute;
    left: 0;
    top: 0;
    height: 10px;
    background-color: #1fd726;
    border-radius: 10px;
}